.modal{
    position: fixed;
    top: 50%; 
    left: 50%; 
    transform: translate(-50%, -50%);
    padding: 40px;
    height: auto;
    border-radius: 20px;
    border: 2px solid black;
    background-color: #F5FBFF;
    max-height: 80vh;
    overflow-y: auto;
  }

.ReactModal__Content {
    max-height: 60vh;
  }

.modal_button{
    position: absolute;
    top: 20px;
    right: 20px;
    padding: 0;
    margin: 0;
    width: 36px;
    height: 36px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
}

